import React, { useState } from 'react';
import './App.css';
import About from './pages/about';
import Projects from './pages/projects';
import sketch from './pages/sketch';
import P5Wrapper from 'react-p5-wrapper';
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";

function App() {
  const [invert, setInvert] = useState(false);

  return (
    <Router>
      <div className="App" id="App" style={invert ? {"-webkit-filter": "invert(100%)","filter": "invert(100%)"} : null}>
          <P5Wrapper sketch={sketch} />
        <Switch>
          <Route path="/about">
            <header className="App-header">
              <h1 className={"App-category-title"}><Link to="/">Home</Link></h1>
              <h1 className={"App-category-title"}><Link to="/projects">Projects</Link></h1>
              <h1 className={"App-category-title-clicked"}>About</h1>
              <h1 className={"App-invert"} onClick={()=>setInvert(!invert)}>{invert ? "Light" : "Dark"}</h1>
            </header>
            <div className="App-body">
              <About />
            </div>
          </Route>
          <Route path="/projects">
            <header className="App-header">
              <h1 className={"App-category-title"}><Link to="/">Home</Link></h1>
              <h1 className={"App-category-title-clicked"}>Projects</h1>
              <h1 className={"App-category-title"}><Link to="/about">About</Link></h1>
              <h1 className={"App-invert"} onClick={()=>setInvert(!invert)}>{invert ? "Light" : "Dark"}</h1>
            </header>
            <div className="App-body">
              <Projects />
            </div>
          </Route>
          <Route exact path="/">
            <header className="App-header">
              <h1 className={"App-category-title-clicked"}>Home</h1>
              <h1 className={"App-category-title"}><Link to="/projects">Projects</Link></h1>
              <h1 className={"App-category-title"}><Link to="/about">About</Link></h1>
              <h1 className={"App-invert"} onClick={()=>setInvert(!invert)}>{invert ? "Light" : "Dark"}</h1>
            </header>
            <div className="App-body">
              <div className="Page-body">
                work in progress
            </div>
            </div>
          </Route>
        </Switch>
        <footer className="App-footer">
          <h1 className="App-category-title">Â© 2021</h1>
        </footer>
      </div>
    </Router >
  );
}
export default App;